import { useReducer } from 'react';
import { Box, Grid, Stack, Toolbar } from "@material-ui/core";
import Nav from "./components/Nav";
import Options from "./components/Options";
import Locations from "./components/Locations";
import wallpaperList from './components/wallpaperList';
import sunny from "./images/weather-icons/sunny.svg";
import { use100vh } from 'react-div-100vh';

export const getTempC = (temp) => ((temp - 32) * (5 / 9)).toFixed(0);
export const getTempF = (temp) => ((temp * (9 / 5) + 32)).toFixed(0);

const tempLocations = [
  {
    city: '8417 Spruce Hill Dr',
    lat: '',
    lon: '',
    key: `${1234}${5678}`,
    current: {
      day: 'Monday',
      date: 'July, 6th, 08:37am',
      timezone: 'America/NewYork',
      sunrise: '08:00am',
      sunset: '07:00pm',
      temp: '0',
      icon: sunny,
      weatherStatus: 'Sunny',
      shortDescription: 'Light rain, gentle breeze.',
      humidity: '100',
      pressure: '100',
      windSpeed: '200',
      visibility: '300'
    },
    daily: [
      {
        date: 'July, 6th, 08:37am',
        day: 'Monday',
        description: 'light rain',
        icon: sunny,
        weatherStatus: 'Sunny',
        humidity: '100',
        windSpeed: '200',
        pressure: '100',
        dayTemp: '0',
        nightTemp: '0',
        mornTemp: '0',
        eveTemp: '0',
        minTemp: '0',
        maxTemp: '0',
      },
    ]
  },
];




const getRandomInt = (max) => {
  return Math.floor(Math.random() * max);
}
const state = { locationList: tempLocations, settings: false, degUnit: 'F', collapseButton: false, wallpaper: wallpaperList[getRandomInt(12)] };
const dispatcher = (state, action) => {
  switch (action.type) {
    case 'toggleCollapseButton':
      return { ...state, collapseButton: !state.collapseButton }
    case 'setWallpaper':
      return { ...state, wallpaper: action.data }
    case 'toggleDegUnit':
      return { ...state, degUnit: state.degUnit === 'F' ? 'C' : 'F' }
    case 'openSettings':
      return { ...state, settings: true };
    case 'closeSettings':
      return { ...state, settings: false }
    default:
      return state;
  }
}


function App() {
  const [appState, appDispatcher] = useReducer(dispatcher, state);
  const height = use100vh()
  return (
    <Grid container direction='column' height={height}>
      <Grid item >
        <Nav appState={appState} appDispatcher={appDispatcher} />
      </Grid>
      <Grid item sx={{ backgroundImage: `url(${appState.wallpaper})`, backgroundSize: 'cover', backgroundPosition: 'center', overflowY: 'auto', flex: '1' }}>
        <Stack spacing={3} mt='2rem' >
          <Locations appState={appState} appDispatcher={appDispatcher} />
          <Locations appState={appState} appDispatcher={appDispatcher} />
        </Stack>
      </Grid>

      <Options appState={appState} appDispatcher={appDispatcher} />
    </Grid>
  );
}

export default App;
