import { useState } from 'react';
import { Grid, Paper, Box, Typography, Stack, IconButton } from "@material-ui/core";
import { getTempC, getTempF } from "../App";
import MoreVertIcon from '@material-ui/icons/MoreVert';

function Locations({ appState, appDispatcher }) {
    const [openOptions, setOpenOptions] = useState(null);
    return (
        <Stack spacing={2} sx={{ backgroundColor: 'blu', px: '0.5rem' }}>
            <Typography variant='h5'
                sx={{
                    backgroundColor: 'black', fontWeight: 'bold', color: 'primary.light',
                    width: 'fit-content', p: '0.3rem', borderRadius: '0.5rem', border: '0.2rem solid', borderColor: 'warning.light'
                }}>Dashboard</Typography>
            <Grid container sx={{overflowX:'auto'}}>
                {appState.locationList.map((item, index) =>
                    <Grid item sx={{width:'fit-content', mb:'1rem'}}>
                        <Paper elevation={3} sx={{ p: '0.6rem', background: 'black', color: 'white', border: '0.13rem solid white', borderRadius: '0.7rem' }}>
                            {/* header */}
                            <Grid container  columnSpacing={2}>
                                <Grid item flexGrow='1' >
                                    <Stack>
                                        <Typography sx={{ color: 'error.light' }}>{item.current.date}</Typography>
                                        <Typography variant='h6' sx={{}}>{item.city}</Typography>
                                    </Stack>
                                </Grid>

                                <Grid item alignSelf='center' flexGrow='0' >
                                    <Stack direction='row' spacing={0.5} justifyContent='between'>
                                        <Typography sx={{ color: 'warning.light', fontWeight: '500', fontSize: '2.5rem' }}>
                                            {appState.degUnit === 'C' ? getTempC(item.current.temp) : getTempF(item.current.temp)}°{appState.degUnit}
                                        </Typography>
                                        <IconButton id="long-button" onClick={(e) => setOpenOptions(e.currentTarget)} sx={{ color: 'white', alignSelf: 'start' }}>
                                            <MoreVertIcon className='text-white hover:text-green-600' />
                                        </IconButton>
                                    </Stack>
                                </Grid>
                            </Grid>

                            {/* body */}
                            <Grid container direction='row' justifyContent='between' width='100%' sx={{ backgroundColor: 'warning.mai' }}>
                                {/* weather Status */}
                                <Grid item flexGrow='0.7' >
                                    <Stack alignItems='center'>
                                        <Box component='img' width='6rem' src={item.current.icon} sx={{mt:'-0.8rem'}} />
                                        <Typography sx={{ mt: '-1.5rem', fontSize: '1.3rem', fontWeight: '500', color: 'warning.light' }}>{item.current.weatherStatus}</Typography>
                                    </Stack>
                                </Grid>

                                {/* timezone status */}
                                <Grid item flexGrow='1' xs>
                                    <Stack alignItems='center'>
                                        <Typography sx={{ fontWeight: '500', color: 'primary.light' }}>TimeZone</Typography>
                                        <Typography sx={{ fontWeight: '400' }}>{item.current.timezone}</Typography>
                                    </Stack>
                                    <Stack direction='row' spacing={5} justifyContent='center'>
                                        <Stack >
                                            <Typography sx={{color:'primary.light', fontWeight:'500'}}>Sunrise</Typography>
                                            <Typography sx={{fontWeight:'400'}}>{item.current.sunrise}</Typography>
                                        </Stack>
                                        <Stack sx={{}}>
                                            <Typography sx={{color:'primary.light', fontWeight:'500'}}>Sunrise</Typography>
                                            <Typography sx={{fontWeight:'400'}}>{item.current.sunrise}</Typography>
                                        </Stack>
                                    </Stack>
                                </Grid>
                            </Grid>
                        </Paper>
                    </Grid>
                )}
            </Grid>
        </Stack>
    )
};

export default Locations;